label input[type='checkbox'] + span {
  position: relative;
  display: block;
  cursor: pointer;
  width: 100%;
  box-sizing: border-box;
  padding: 4% 2% 4% 15%;
  border-bottom: 2px solid transparent;
  transition: border 0.4s ease, background 0.6s ease;
}
label input[type='checkbox'] + span:hover {
  background: #ffffff;
}
label input[type='checkbox'] + span:hover:before {
  content: "\f14a";
}
label input[type='checkbox'] + span:before {
  content: "\f096";
  color: #999999;
  transition: color 0.4s ease;
  z-index: 2;
}
label input[type='checkbox'] + span:after {
  content: "";
  transition: text-shadow 0.4s ease, color 0.4s ease;
  color: #ffffff;
  z-index: 1;
  text-shadow: 0px 0px 0 #98165d, 0px 0px 0 #98165d, 0px 0px 0 #98165d, 0px 0px 0 #98165d;
}
label input[type='checkbox'] + span:after,
label input[type='checkbox'] + span:before {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.4em;
  position: absolute;
  width: 2%;
  left: 2%;
  top: 50%;
  transform: translateY(-50%);
}
label input[type='checkbox'] + span:after.pull-left,
label input[type='checkbox'] + span:before.pull-left {
  margin-right: .3em;
}
label input[type='checkbox'] + span:after.pull-right,
label input[type='checkbox'] + span:before.pull-right {
  margin-left: .3em;
}
label input[type='checkbox']:checked + span {
  background: #ffffff;
  border-bottom: 2px solid #e5e5e5;
  color: #98165d;
}
label input[type='checkbox']:checked + span:before {
  content: "\f14a";
  color: #ffd200;
}
label input[type='checkbox']:checked + span:after {
  content: "\f0c8";
  color: #98165d;
  text-shadow: 2px 2px 0 #98165d, -2px -2px 0 #98165d, 2px -2px 0 #98165d, -2px 2px 0 #98165d;
